/* ny css */

.settings-container {
    display: grid;
    grid-template-areas: ' indstillinger' ' knapper ';
    /* background: url("/src/img/indstillingerbg.svg"); */
    /* padding: 1rem; */
}

.indstillinger {
    grid-area: indstillinger;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-content: center;
    justify-content: space-between;
    padding: 1em 2em;
    max-width: 40em;
    margin: 1em auto;
    width: 25em;
    height: auto;
}

.indstillinger div {
    padding: 0.2em;
}

.knapper {
    grid-area: knapper;
    padding: 0em 1em 0.75em 1em;
    align-items: end;
    /* display: grid; */
    /* grid-template-rows: 1fr;
    grid-template-columns: 1fr; */
    margin-top: auto;
}


/* 
.indstillinger div:nth-child(odd) {
    text-align: left;
} */

.knap {
    justify-items: end;
    display: grid;
    align-content: center;
}


/* slut ny css */


/* ON / OFF */

.switch {
    position: relative;
    display: inline-block;
    max-width: 60px;
    height: 34px;
    /* giver den mere bredde, og s√• siger den maks kan bruge 60px af det */
    width: 100%;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #2c2c2c;
    -webkit-transition: .4s;
    transition: .4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
}

input:checked+.slider {
    background-color: #F89B30;
}

input:focus+.slider {
    box-shadow: 0 0 1px #F89B30;
}

input:checked+.slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}


/* Rounded sliders */

.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}