/* ny css  */

.login-container {
    display: grid;
    grid-template-areas: 'wrapper' 'knapper';
}

.login-wrapper {
    grid-area: wrapper;
    padding: 1em;
    display: grid;
}

.glemt {
    text-align: end;
    align-content: center;
    height: 1em;
    margin-top: -1em;
    margin-bottom: 1em;
}


/* slut ny css */

input {
    border-radius: 5px;
    border: 1px solid rgb(190, 190, 190);
    width: 100%;
    padding: 0.6rem 0em;
    margin-bottom: 20px;
}


/* har du glemt dit login? */

.opret {
    text-align: center;
    text-align: right;
    font-weight: bold;
}

.opret:hover {
    color: #F89B30;
}

.velkommen {
    text-align: center;
    padding: 2% 0em;
    text-transform: uppercase;
}

.login-knap {
    text-decoration: none;
    color: #2c2c2c;
    background-color: #F89B30;
    border-radius: 6px;
    border: none;
    width: 100%;
    height: 50px;
    text-transform: uppercase;
    font-weight: bold;
    transition: 0.5s;
}

.login-knap:hover {
    transition: 0.5s;
    color: #2c2c2c;
    background-color: rgb(219, 219, 219);
}